body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
@import url("https://fonts.googleapis.com/css2?family=Sacramento&display=swap");n
.container, .button, .moon,.sml, .button span{
	position: absolute;
}

.container{
	margin: auto;
	top: 0;
	right: 0;
	bottom:0;
	left: 0;
	width: 200px;
	height: 200px;
	/*background: linear-gradient(#ff0066 , #cc0052);*/
}
.moon{
	width: 85px;
	height: 85px;
	background: #eee;
	border-radius: 50%;
	left: 175px;
	top:2px;
	transition:all 1s ease-in-out;
	z-index: 2;
}
.moon:before{
	position: absolute;
	content: '';
	width: 20px;
	height: 22px;
	border-radius: 50%;
	background: #ccc;
  top: 25px;
  left:12px;
  box-shadow: 10px -20px 0 -4px #ccc, 4px 18px 0 -6px #ccc;
}


a:hover .moon{
	left: 215px;
	top: -35px;
	box-shadow: 0px 0px 10px #f2f2f2;
	transition: all 1s ease-in-out;
}

.button{
	width: 250px;
	height: 100px;
	background: #173356 ;
	box-shadow: 0px 0px 10px #fff;
	border-radius: 2px;
	z-index: 4;
  top:45px;
  left:-20px;
	transition: all 1s ease-in-out;
   
}
a:hover .button{
	transition: all 1s ease-in-out;
	box-shadow: 0px 2px 18px #eee;
}
a:hover .star{
	animation: twinkle 1s infinite;
}
.star{
	position: absolute;
	width: 2px;
	height: 2px;
	background: #fff;
	border-radius: 50%;
	z-index: 2;
	opacity: 0.1;
	top: 5px;
	left: 10px;
	box-shadow: 30px 20px 0px #fff, 
				40px 60px 0px #fff
				,40px 60px 0px #fff,
				80px 50px 0px #fff,
				120px 20px 0px #fff,
				110px 60px 0px #fff,
				120px 5px 0px #fff,
				200px 5px 0px #fff,
				210px 25px 0px #fff,
				230px 50px 0px #fff,
				170px 90px 0px #fff,
				190px 74px 0px #fff,
				140px 80px 0px #fff,
				20px 90px 0px #fff,
				40px 760px 0px #fff,
				60px 70px 0px #fff,
				80px 80px 0px #fff,
				160px 20px 0px #fff;
}
.s2 {
	position: absolute;
	height: 3px ;
	width: 3px;
	border-radius: 50%;
	background: #fff;
	animation-delay: 0.5s;
	box-shadow: 60px 5px 0px #fff, 
				40px 30px 0px #fff,
				10px 40px 0px #fff,
				90px 40px 0px #fff,
				100px 10px 0px #fff,
				110px 5px 0px #fff,
				7px 20px 0px #fff,
				200px 50px 0px #fff,
				180px 80px 0px #fff,
				190px 15px 0px #fff;
	top: 10px;
}
.s3{
	position: absolute;
	height: 1px ;
	width: 1px;
	border-radius: 50%;
	background: #fff;
	animation-delay: 0.9s;
	box-shadow: 30px 10px 0px #fff,
				 80px -20px 0px #fff
				,120px 10px 0px #fff,
				12px 50px 0px #fff,
				25px 190px 0px #fff,
				65px 840px 0px #fff,
				125px 70px 0px #fff,
				160px 20px 0px #fff,
				160px -7px 0px #fff,
				200px -18px 0px #fff,
				225px -38px 0px #fff,
				240px -40px 0px #fff;
	top: 30px;
}
@keyframes twinkle{
	50%{
		opacity: 1;
	}
}
.button span{
	text-align:center;
  font-weight:bold;
	font-family:  "Sacramento", cursive;
	font-size: 50px;
	left: 60px;
	line-height: 70px;
	margin: 15px auto;
	transition: all 1px ease-in-out;
	background: -webkit-linear-gradient(#d9d9d9,  #cccccc);
	-webkit-background-clip:text;
	-webkit-text-fill-color:transparent;
	font-weight: bold;
}
img{
  max-width: 1000px;
  border: 5px solid white; 
  height: 700px;
}
.parent{
  position: relative;
  text-align: center;
  
}
