.stop{
  position: absolute;
  left: 0;
  top: 200px;
  width: 150px;
  height: 150px;
  font-weight: 900;
	background: #e22222 ;
	box-shadow: 0px 0px 10px #fff;
	border-radius: 50%;
	z-index: 4;
  transition: all 1s ease-in-out;
  font-family:  "Sacramento", cursive;
  font-size: 50px;
}
.stop:hover {
  background: orange;
  font-size: 70px;
}

